<template>
  <div class="page-login">
    <header class="login-header">
      <div class="logo"></div>
    </header>
    <div class="login-panel">
      <div class="banner">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt=""
        />
      </div>
      <div class="form">
        <p class="tips" v-if="error">{{error}}</p>
        <p>
          <span></span>
        </p>
        <el-input :class="{error : error && !userName}" placeholder="//" prefix-icon="profile" v-model="userName"></el-input>
        <el-input :class="{error : error && !passWord}" placeholder="" prefix-icon="password" v-model="passWord" type="password"></el-input>
        <div class="foot">
          <a></a>
        </div>
        <el-button type="primary" class="btn-login" @click="submit"></el-button>
        <p class="reg">
          <span>?</span>
          <router-link :to="{name : 'register'}"></router-link>
        </p>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title"></span>
          </h3>
          <div class="oauth cf">
            <a
              class="oauth__link oauth__link--qq"
              href="/account/connect/tencent"
              data-mtevent="{&quot;la&quot;:&quot;oauth/qq&quot;}"
              target="_blank"
            ></a>
            <a
              class="oauth__link oauth__link--weibo"
              href="/account/connect/sina"
              data-mtevent="{&quot;la&quot;:&quot;oauth/sina&quot;}"
              target="_blank"
            ></a>
          </div>
        </div>
      </div>
    </div>
    <footer>
        <ul>
            <li><a href="#"></a></li>
            <li><a href="#"></a></li>
            <li><a href="#"></a></li>
            <li><a href="#"></a></li>
            <li><a href="#"></a></li>
        </ul>
        <p>2019  meituan.com ICP070791 11010502025545</p>
    </footer>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data() {
    return {
      userName: "",
      passWord: "",
      error: ''
    };
  },
  methods: {
    submit() {
      if(!this.userName){
        this.error = '';
        return false;
      }
      if(!this.passWord){
        this.error = '';
        return false;
      }
      api.login({
        params: {
          userName: this.userName,
          password: this.passWord
        }
      }).then((res) => {
        console.log(res);
        if(res.data.status == 'success') {
          this.$router.push({name: 'index'});
          this.$store.commit('setUserName', this.userName);
        }else {
          this.error = res.data.msg;
        }
      })
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>


